<template>
  <div class="operation-control">
    <div class="opt-top">
      <div class="search-flex" v-if="!rowSelectKeys.length">
        <a-input-search style="width:266px;" placeholder="商品名称/编号/条码/关键字" class="add-input search-box" v-model="keyWords" @search="handleSearch">
          <a-button slot="enterButton">
            搜索
          </a-button>
        </a-input-search>
        <a-select class="search-box" style="width: 120px" placeholder="商品分类" v-model="item" @change="handleSelectItem">
          <a-select-option v-for="(item, index) in options.item" :value="item.id" :key="index">{{ item.value }}</a-select-option>
        </a-select>
        <a-select class="search-box" style="width: 120px" placeholder="商品品牌" v-model="brand" @change="handleSelectBrand">
          <a-select-option v-for="(item, index) in options.brand" :value="item.id" :key="index">{{ item.value }}</a-select-option>
        </a-select>
        <a-select class="search-box" style="width: 120px" placeholder="上下架" v-model="sale" @change="handleSelectSale">
          <a-select-option v-for="(item, index) in options.sale" :value="item.id" :key="index">{{ item.value }}</a-select-option>
        </a-select>
      </div>
      <div class="select-list" v-if="rowSelectKeys.length">
        <div class="select-box">
          <icon-font-box type="icon_warning" size="16" color="#378EFB" />已选<span class="select-num">{{ rowSelectKeys.length }}</span>条
        </div>
        <div class="select-box" @click="$emit('optClick', 'price')">
          <icon-font-box type="iconxiugai" size="16" color="#378EFB" />调价
        </div>
        <div class="select-box" @click="$emit('optClick', 'refresh')">
          <icon-font-box type="iconyemianshuaxin" size="16" color="#52C41A" />刷新
        </div>
        <div class="select-box" @click="$emit('optClick', 'exportPrice')">
          <icon-font-box type="icondaochu" size="16" color="#F7B500" />导出商品价格
        </div>
        <div class="select-box" @click="$emit('optClick', 'exportLonePrice')">
          <icon-font-box type="icondaochu" size="16" color="#F7B500" />导出客户单独定价
        </div>
      </div>
      <div class="other-btn">
        <a-popover placement="bottom" trigger="click">
          <template slot="content">
            <p class="right-menu-item" @click="$emit('optClick', 'priceBatchVisible')">Excel导入商品价格</p>
            <p class="right-menu-item" @click="$emit('optClick', 'customPriceBatchVisible')">Excel导入客户单独定价</p>
          </template>
          <a-button>批量导入</a-button>
        </a-popover>
      </div>
    </div>
    <div class="opt-bottom" v-if="selectListKeys.length && !rowSelectKeys.length">
      <div class="tag-box" v-for="(item, index) in selectList" :key="index">
        {{ item.label }}<span class="tag-info">{{ item.value }}</span>
        <icon-font-box type="icon_close" size="14" class="tag-close" @click="handleDelete(item.key)"></icon-font-box>
      </div>
      <a-button type="link" @click="handleDelete('all')">全部清除</a-button>
    </div>
  </div>
</template>

<script>
import { IconFontBox } from '@handday/components'
export default {
  name: 'OperationPlatform',
  components: {
    IconFontBox
  },
  props: {
    rowSelectKeys: {
      type: Array,
      default: () => []
    }
  },
  data () {
    return {
      keyWords: undefined,
      item: undefined, // 分类
      brand: undefined,
      sale: undefined,
      selectList: {},
      options: {
        item: [
          { id: 1, value: '指掌' },
          { id: 2, value: '管家婆' }
        ],
        brand: [
          { id: 1, value: '指掌' },
          { id: 2, value: '管家婆' }
        ],
        sale: [
          { id: 0, value: '全部' },
          { id: 1, value: '上架' },
          { id: 2, value: '下架' }
        ]
      }
    }
  },
  computed: {
    selectListKeys () {
      return Object.keys(this.selectList)
    }
  },
  methods: {
    handleDelete (name) {
      console.log(name)
      if (name === 'all') {
        // 删除全部
        this.selectList = {}
        this.keyWords = ''
        this.item = '' // 分类
        this.brand = ''
        this.sale = 0
      } else {
        // 处理对应的数据
        const lst = this.selectList
        delete lst[name]
        console.log(lst)
        this.selectList = {
          ...lst
        }
        this[name] = ''
      }
      this.$emit('change', this.selectList)
    },
    handleSearch (value) {
      console.log('search', value)
      this.$set(this.selectList, 'keyWords', {
        label: '关键字',
        value: this.keyWords,
        key: 'keyWords'
      })
      this.$emit('change', this.selectList)
    },
    handleSelectItem (value, options) {
      console.log(value, options)
      this.options.item.forEach(item => {
        if (item.id === value) {
          this.$set(this.selectList, 'item', {
            ...item,
            label: '商品分类',
            key: 'item'
          })
        }
      })
      this.$emit('change', this.selectList)
    },
    handleSelectBrand (value) {
      this.options.brand.forEach(item => {
        if (item.id === value) {
          this.$set(this.selectList, 'brand', {
            ...item,
            label: '品牌',
            key: 'brand'
          })
        }
      })
      this.$emit('change', this.selectList)
    },
    handleSelectSale (value) {
      this.options.sale.forEach(item => {
        if (item.id === value) {
          this.$set(this.selectList, 'sale', {
            ...item,
            label: '是否上架',
            key: 'sale'
          })
        }
      })
      this.$emit('change', this.selectList)
    }
  }
}
</script>

