  border-bottom:2px solid #378EFB;
  padding:24px;
  .opt-top{
    display:flex;
    justify-content: space-between;
    .search-flex{
      .search-box{
        margin-right:16px;
      }
    }
    .select-list{
      display: flex;
      flex-grow: 1;
      flex-shrink: 0;
      align-items: center;
      .select-box{
        margin-right:24px;
        cursor: pointer;
        .icon-font-wrap{
          vertical-align: sub;
          margin-right:4px;
        }
      }
    }
    .other-btn{
      flex-shrink: 0;
    }
  }
  .opt-bottom{
    display: flex;
    padding: 16px 0 0;
    .tag-box{
      height:32px;
      background: #f5f9ff;
      border-radius:50px;
      padding:4px 36px 4px 12px;
      position: relative;
      line-height:24px;
      font-size:12px;
      .tag-info{
        color:#378EFB;
        padding-left:4px;
      }
      .tag-close{
        color:#378EFB;
        position:absolute;
        right:12px;
        top:10px;
        cursor: pointer;
      }
    }
  }
}
